/* Form Container */
body {
  height: 100vh; /* Set the height of the body to 100% of the viewport */
  margin: 0; /* Remove default body margin */
  display: flex;
  align-items: center; /* Center vertically */
  justify-content: center; /* Center horizontally */
}

.box {
  max-width: 400px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 100%; /* Adjust the width if needed */
}

/* Rest of your existing styles remain unchanged */


/* Form Heading */
.heading {
  text-align: center;
  color: #333;
}

/* Input Styles */
input {
  width: 100%;
  padding: 10px;
  margin: 8px 0;
  box-sizing: border-box;
}

/* Button Styles */
.submit-button {
  background-color: #4caf50;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-bottom: 20px;
  margin-right: 10px
}

.button {
  background-color: #008CBA;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 10px;
}

.button:hover,
.submit-button:hover {
  background-color: #45a049;
}

.form-bottom{
  cursor: pointer;
}
/* Optional: Add media queries for responsiveness */
@media (max-width: 600px) {
  .box {
    width: 80%;
  }
}
