
@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@400;600&family=Mulish:wght@200;300;400;500&display=swap');

*{
  margin: 0px;
  padding: 0px;
  font-family: 'Exo 2', sans-serif;
font-family: 'Mulish', sans-serif;
  
}
.code{
  height: 60vh;
  width: 96vw;
  border-radius: 5px;
 padding: 15px ;
 margin: 5px 5px !important;
}
.output{
  height: 10vh;
  width: 50vw;
  border: 1px solid black;
  padding: 15px;
  margin: 5px 5px !important;
}

.input{
  width: 40vw;
  height: 10vh;
  padding: 15px;
  padding: 15px;
  margin: 5px 5px !important;
}



/* Dashboard container */
.dashboard-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  padding: 20px;
  margin: 0px;
  gap: 4px;

  
}

/* Left side (user information) */
.left {
  /* Style for desktop mode */
  align-items: center;
  justify-content: center;
}

/* Right side (PieChart) */
.right {
  /* Style for desktop mode */
  align-items: center;
  justify-content: center;
}
.right h1{
  align-items: center;
}

.footer{
  display: flex;
  justify-content:space-between;
}

.username{
  cursor: pointer;
  font-size: 1.5em;
  color:#D2DE32;
  font-weight: 600;
}

.Navbar-buttons :hover{
  color: #FB2576 !important; 
}
